html {
  box-sizing: border-box;
  font-size: 100%;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  padding: 20px;
  background-color: yellow;
  color: #000;
  font-family: 'helvetica', sans-serif;
  line-height: 1.3;
}

.content {
  background-color: #fff;
  padding: 20px;
}